import './App.css';
import React from "react";
import axios from "axios";

import 'bootstrap/dist/css/bootstrap.min.css';
import  Boot_Button from 'react-bootstrap/Button'

import { TextInput, Button, StyleSheet } from "react-native";
import FinalText from "./FinalText";


const styles = StyleSheet.create({
  'input': {
    height: 30,
    margin: 12,
    borderWidth:1,
    color: '#1ADE1C'
  },
})


function App() {
  const [textInput, onChangeTextInput] = React.useState('')
  const [text, setText] = React.useState('tester69123')

  function ProcessInput() {
    if (textInput!=='') {
      console.log(textInput)

      const check = async() => {
        try {
          await axios.get('https://da.gd/s/?url='+textInput)
            .then(res => {
              console.log(res)
              setText(res['data'])
            })
        } catch(err) {
          setText('Error 400 - Bad URL')
      }}
      check()
    }
  } 

  return (
    <div className="App">
      <TextInput
        placeholder='Enter URL Here'
        style={styles.input}
        onChangeText={onChangeTextInput}
      />

      <Button 
        title="Shorten"
        color="#841584"
        onPress={ProcessInput}
      />

      <Boot_Button variant="primary" size="lg">
        ???
      </Boot_Button>

      <FinalText
        text={text}
      />

    </div>
  );
}



export default App;
